/landing_page.scss';\r\n  @import './desktop/components/desktop_side_nav.scss';\r\n}\r\n\r\n// Mobile styles\r\n@media (max-width: $DESKTOP_WIDTH - 1) {\r\n  @import './mobile/index.scss';\r\n  @import './mobile/components/landing_page.scss';\r\n  @import './mobile/components/mobile_open_menu.scss';\r\n  @import './mobile/components/mobile_menu_line_list.scss';\r\n  @import './mobile/components/mobile_menu_nav_list.scss';\r\n}","/**\r\n * Styles for the main site\r\n */\r\n\r\nbody {\r\n  color: white;\r\n  background-color: black;\r\n  font-family: 'Sprat Type';\r\n  height: 100vh;\r\n  width: 100%;\r\n}\r\n\r\n.mobile {\r\n  display: none;\r\n}","/**\r\n * Landing page navbar\r\n * https://github.com/EthanNakache/Sprat-type\r\n */\r\n\r\n.navbar {\r\n  width: 100vw - $X_MARGIN;\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 12px 0 12px 0;\r\n\r\n  // Put this navbar at the bottom\r\n  position: fixed;\r\n  bottom: 5vh;\r\n\r\n  .diamond {\r\n    margin: auto;\r\n    width: 8.8px;\r\n    height: 8.8px;\r\n    background-color: white;\r\n    transform: rotate(45deg);\r\n    margin-bottom: 10px;\r\n    visibility: hidden;\r\n\r\n    &.selected {\r\n      visibility: visible;\r\n    }\r\n  }\r\n\r\n  .navbar-item {\r\n    font-size: $FONT_SIZE_NAV;\r\n    cursor: pointer;\r\n    text-decoration: none;\r\n    transform: $FONT_SPRAT_STRETCH;\r\n    font-weight: $FONT_SPRAT_WEIGHT;\r\n    text-underline-offset: 20px;\r\n\r\n    &.selected {\r\n      text-decoration: underline;\r\n    }\r\n  }\r\n\r\n}\r\n","/**\r\n * Constants for stylesheet\r\n */\r\n\r\n@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@200;300;400&family=Overpass+Mono:wght@300;400;600;700&display=swap');\r\n@import url('https://use.typekit.net/zhb6cru.css');\r\n\r\n// sizing\r\n$X_MARGIN: 10vw;\r\n$TOP_MARGIN: 36px;\r\n$DESKTOP_WIDTH: 768px;\r\n$LINE_ELEMENT_HEIGHT: 142px;\r\n$NAV_ELEMENT_HEIGHT: 126px;\r\n\r\n// font families\r\n$FONT_SUBHEAD: 'Overpass Mono', monospace;\r\n$FONT_BODY: 'franklin-gothic-urw', sans-serif;;\r\n\r\n// font sizes\r\n$FONT_SIZE_NAV: 36px;\r\n$FONT_SIZE_LINK: 26px;\r\n$FONT_SIZE_LINE: 114px; // changed from 144px\r\n$FONT_SIZE_DESIGNER: 36px;\r\n$FONT_SIZE_TITLE: 48px;\r\n$FONT_SIZE_BODY: 18px;\r\n$FONT_SIZE_SUPERSCRIPT: 18px;\r\n\r\n// font adjustments and weights\r\n$FONT_SPRAT_STRETCH: scaleY(1.2);\r\n$FONT_SPRAT_WEIGHT: 300;\r\n$FONT_TITLE_WEIGHT: 400;\r\n$FONT_LINK_WEIGHT: 400;\r\n\r\n// letter spacing\r\n$HEADER_TRACKING: -15px;\r\n$TITLE_TRACKING: -5px;\r\n$SUBHEAD_TRACKING: -2px;\r\n","/**\r\n * First page the user sees, the landing page.\r\n *\r\n * Contains a 3D asset in the back.\r\n */\r\n\r\n.landing-page-background {\r\n  background-color: black;\r\n  width: 100%;\r\n  height: 100vh;\r\n\r\n  #landing-page-cube {\r\n    width: 100%;\r\n    height: 100vh;\r\n  }\r\n}\r\n\r\n.landing-page {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n\r\n  #curr-line {\r\n    position: absolute;\r\n    left: $X_MARGIN/2;\r\n    bottom: 40%;\r\n    display: block;\r\n\r\n    #line-name {\r\n      font-size: $FONT_SIZE_LINE;\r\n      text-transform: uppercase;\r\n      transform: $FONT_SPRAT_STRETCH;\r\n      font-weight: $FONT_SPRAT_WEIGHT;\r\n      letter-spacing: $HEADER_TRACKING;\r\n    }\r\n\r\n    #designers-name {\r\n      font-size: $FONT_SIZE_DESIGNER;\r\n      font-family: $FONT_SUBHEAD;\r\n      text-transform: uppercase;\r\n      letter-spacing: $SUBHEAD_TRACKING;\r\n    }\r\n  }\r\n\r\n  .title-theme {\r\n    position: absolute;\r\n    top: $TOP_MARGIN;\r\n    font-size: $FONT_SIZE_TITLE;\r\n    transform: $FONT_SPRAT_STRETCH;\r\n    font-weight: $FONT_TITLE_WEIGHT;\r\n    letter-spacing: $TITLE_TRACKING;\r\n  }\r\n\r\n  .logo {\r\n    position: absolute;\r\n\r\n    // Gonna be totally honest I don't think this is _quite_ accurate to get 21px under the logo\r\n    top: $TOP_MARGIN;\r\n    right: 24px;\r\n    width: 61px;\r\n    height: 80px;\r\n  }\r\n}","/**\r\n * Navigation sidebar on desktop\r\n */\r\n\r\n.links {\r\n  position: absolute;\r\n  top: $TOP_MARGIN;\r\n  left: .65*$X_MARGIN/2; // an aprox guess to get the actual links aligned with the rest of the page\r\n\r\n  .link {\r\n    font-family: $FONT_BODY;\r\n    font-size: $FONT_SIZE_LINK;\r\n    text-transform: uppercase;\r\n    font-weight: $FONT_LINK_WEIGHT;\r\n    display: block;\r\n    color: white;\r\n    text-decoration: none;\r\n    padding-bottom: 10px;\r\n  }\r\n\r\n  .linkNum {\r\n      font-size: $FONT_SIZE_SUPERSCRIPT;\r\n  }\r\n}\r\n","/**\r\n * Mobile styles in general for the site\r\n */\r\n\r\nhtml {\r\n  height: 100%;\r\n}\r\n\r\nbody {\r\n  color: white;\r\n  background-color: black;\r\n  font-family: 'Sprat Type';\r\n  height: 100%;\r\n  width: 100%;\r\n\r\n  #root {\r\n    height: 100%;\r\n  }\r\n}\r\n\r\n.desktop {\r\n  display: none;\r\n}","\r\n// TODO: unsure why this font is so much smaller than figma\r\n$FONT_SIZE_TITLE: 48px;\r\n\r\n/**\r\n * 3D model in the landing page.\r\n */\r\n.landing-page-background {\r\n  z-index: -1;\r\n  position: fixed;\r\n  background-color: black;\r\n  width: 100%;\r\n  height: 100%;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  #landing-page-cube {\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n\r\n  &.mobile-line-menu-open {\r\n    top: 0px;\r\n    height: 200px;\r\n  }\r\n\r\n  &.mobile-nav-menu-open {\r\n    bottom: 0px;\r\n    height: 200px;\r\n  }\r\n}\r\n\r\n.landing-page {\r\n  position: relative;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  height: 100%;\r\n  width: 100%;\r\n\r\n  &.mobile-line-menu-open {\r\n    position: static;\r\n  }\r\n\r\n  .title-theme {\r\n    position: fixed;\r\n    font-size: $FONT_SIZE_TITLE;\r\n    // Vertically stretch the font\r\n    transform: scale(1, 1.5);\r\n    transition-duration: 1s;\r\n    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n    &.mobile-line-menu-open {\r\n      // Title becomes a bit smaller on the mobile menu open\r\n      font-size: $FONT_SIZE_TITLE * 0.8;\r\n      top: 90px;\r\n    }\r\n\r\n    &.mobile-nav-menu-open {\r\n      // Title becomes a bit smaller on the mobile menu open\r\n      font-size: $FONT_SIZE_TITLE * 0.8;\r\n      bottom: 60px;\r\n    }\r\n  }\r\n\r\n  // Hide desktop elements\r\n  .link {\r\n    display: none;\r\n  }\r\n\r\n  .lineLabel {\r\n    display: none;\r\n  }\r\n\r\n  #label {\r\n    display: none;\r\n  }\r\n\r\n  #curr_line {\r\n    display: none;\r\n  }\r\n\r\n  .logo {\r\n    position: fixed;\r\n    bottom: 21px;\r\n    width: 69px;\r\n    height: 90px;\r\n\r\n    &.mobile-line-menu-open {\r\n      display: none;\r\n    }\r\n\r\n    &.mobile-nav-menu-open {\r\n      display: none;\r\n    }\r\n  }\r\n}\r\n","/**\r\n * Component on mobile that opens the menu\r\n */\r\n\r\n#mobile-open-menu {\r\n  position: fixed;\r\n  top: 34px;\r\n  width: 37px;\r\n  height: 37px;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n\r\n  #mobile-open-menu-sign {\r\n    font-size: 40px;\r\n    margin-bottom: 10px;\r\n  }\r\n\r\n  #mobile-open-menu-border {\r\n    position: absolute;\r\n    border: 1px solid white;\r\n    transform: rotate(45deg);\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n}","/**\r\n * Styles for the menu that shows all the lines in the show\r\n */\r\n\r\n#mobile-menu-line-list {\r\n  position: fixed;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  // Initially, the line menu is hidden and below\r\n  transform: translateY($LINE_ELEMENT_HEIGHT*11);\r\n\r\n  &.mobile-line-menu-open {\r\n    position: absolute;\r\n    transform: translateY($LINE_ELEMENT_HEIGHT*7);\r\n  }\r\n\r\n  .line-element {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    height: $LINE_ELEMENT_HEIGHT;\r\n    width: 100%;\r\n    border-top: 1px solid white;\r\n    background-color: black;\r\n    cursor: pointer;\r\n\r\n    .bullet {\r\n      width: 8px;\r\n      height: 8px;\r\n      background-color: white;\r\n      margin: 9px;\r\n      transform: rotate(45deg);\r\n    }\r\n\r\n    .line-element-body {\r\n      margin-top: 32px;\r\n\r\n      .title {\r\n        font-size: 36px;\r\n        transform: scale(1, 1.2);\r\n      }\r\n\r\n      .designers {\r\n        height: 16px;\r\n        margin-top: 16px;\r\n        font-size: 16px;\r\n      }\r\n    }\r\n  }\r\n}","/**\r\n * Styles for the menu that shows all the nav links\r\n */\r\n\r\n#mobile-menu-nav-list {\r\n  position: relative;\r\n  width: 100%;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  // Initially, the line menu is hidden and below\r\n  opacity: 0;\r\n  z-index: -2;\r\n\r\n  &.mobile-nav-menu-open {\r\n    opacity: 1;\r\n    z-index: 2;\r\n  }\r\n\r\n  .nav-element {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    height: $NAV_ELEMENT_HEIGHT;\r\n    width: 100%;\r\n    border-top: 1px solid white;\r\n    background-color: black;\r\n    cursor: pointer;\r\n    text-decoration: none;\r\n\r\n    &.end {\r\n      border-bottom: 1px solid white;\r\n    }\r\n\r\n    .bullet {\r\n      width: 8px;\r\n      height: 8px;\r\n      background-color: white;\r\n      margin: 9px;\r\n      transform: rotate(45deg);\r\n    }\r\n\r\n    .nav-element-body {\r\n      .title {\r\n        color: white;\r\n        font-size: 36px;\r\n        transform: scale(1, 1.2);\r\n      }\r\n    }\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
